body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  position: fixed;
  top: 0;
  height: 70px;
  background-color: white;
  width: 112%;
  display: flex;
  align-items: flex-end;
  /* justify-content: center; */
  box-shadow: 1px 1px 1px 1px rgba(116, 110, 110, 0.199);
}
#section1,
#section2,
#section3,
#section4,
#section5 {
  padding-top: 75px;
}
.header-inner {
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.hamburger-btn {
  font-size: 1.3rem;
  position: absolute;
  bottom: 0;
  width: 40px;
  height: 35px;
  right: 110px;
  outline: none;
  background-color: rgb(134, 125, 125);
  color: whitesmoke;
  margin-bottom: 17px;
}
.addTransiton {
  -webkit-transform: translateX(170px);
          transform: translateX(170px);
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
  transition: -webkit-transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}
.removeTransition {
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
  transition: -webkit-transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}

#sidebar-list {
  background-color: rgb(134, 125, 125);
  height: 200px;
  width: 238px;
  position: absolute;
  z-index: 2000;
  right: 0;
  top: 0;
  margin-top: 70px;
  padding-right: 74px;
}
.nav-big {
  list-style: none;
  display: flex;
  justify-content: space-around;
  width: 70%;
  font-weight: bold;
}

.nav-big-link {
  cursor: pointer;
  color: white;
  text-decoration: none !important;
}

.nav-small {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: space-around;
  margin: auto;
  height: 40%;
  margin-top: 63px;
  width: 80%;
}
.nav-small-link {
  cursor: pointer;
  color: whitesmoke;
  padding-bottom: 15px;
}

:root {
  --off-white: #fafafa;
}
.nav {
  background-color: #fafafa;
  background-color: var(--off-white);
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 80px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

.nav-logo {
  height: 60px;
  width: 60px;
}

.nav-logo:hover {
  cursor: pointer;
}

.nav .nav-content {
  max-width: 900px;
  padding: 0rem 3rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.nav-item {
  display: inline;
  margin-left: 2rem;
  color: #333;
}

.nav-item:hover {
  color: black;
  cursor: pointer;
}

.section {
}

.section-dark {
  background: #333;
  color: white;
}

.section-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px;
}

.nav-item > .active {
  border-bottom: 1px solid #333;
}
